import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import FrownO from '@wind/icons/lib/icons/FrownO';
import Spin from '../spin';
const defaultLocale = {
    emptyText: <span><FrownO />暂无数据</span>,
};
function getRowKey(key, dataIndex, isTitle) {
    const ret = key == null ? dataIndex : key;
    if (ret != null) {
        return isTitle ? `${ret}-title` : ret;
    }
    return undefined;
}
export default class HorizontalTable extends React.Component {
    constructor(props) {
        super(props);
        this.generateTable = () => {
            const { rows, dataSource, prefixCls } = this.props;
            const locale = this.getLocale();
            if (dataSource) {
                let trs = [];
                rows && rows.forEach((row, index) => {
                    let innerRow = [];
                    let len = row.length;
                    row.forEach((item, index) => {
                        const { dataIndex, key, titleWidth, contentWidth, render, title, className, colSpan, titleAlign, contentAlign } = item;
                        let titleStyle = {
                            width: titleWidth,
                            textAlign: titleAlign
                        };
                        let contentStyle = {
                            width: contentWidth,
                            textAlign: contentAlign
                        };
                        innerRow.push(<td key={getRowKey(key, dataIndex, true)} className={classNames(className, `${prefixCls}-item-title`)} style={titleStyle}>
              {title}
            </td>);
                        innerRow.push(<td key={getRowKey(key, dataIndex, false)} colSpan={colSpan} className={classNames(className, {
                            [`${prefixCls}-last-column`]: index === len - 1,
                            [`${prefixCls}-item-content`]: true,
                        })} style={contentStyle}>
            {typeof render === 'function' ? render(dataSource[dataIndex], dataSource, index) : dataSource[dataIndex]}
          </td>);
                    });
                    trs.push(<tr key={index} className={`${prefixCls}-row ${prefixCls}-row-${index % 2 === 0 ? 'odd' : 'even'}`}>{innerRow}</tr>);
                });
                return <table>
        <tbody className={`${prefixCls}-tbody`}>
          {trs}
        </tbody>
      </table>;
            }
            return locale.emptyText;
        };
    }
    getLocale() {
        let locale = {};
        if (this.context.antLocale && this.context.antLocale.Table) {
            locale = this.context.antLocale.Table;
        }
        return Object.assign(Object.assign(Object.assign({}, defaultLocale), locale), this.props.locale);
    }
    render() {
        const { prefixCls, className, style, title, striped, bordered, size } = this.props;
        let loading = this.props.loading;
        if (typeof loading === 'boolean') {
            loading = {
                spinning: loading,
            };
        }
        return (<div className={classNames(`${prefixCls}-horizontal ${prefixCls}`, className, {
            [`${prefixCls}-without-column-header`]: true,
            [`${prefixCls}-striped`]: striped,
            [`${prefixCls}-bordered-${bordered}`]: !!bordered,
            [`${prefixCls}-${size}`]: !!size,
        })} style={style}>
        {title ? <div className={`${prefixCls}-title`}>{title}</div> : null}
        <Spin {...loading}>
          <div className={`${prefixCls}-body`}>
            {this.generateTable()}
          </div>
        </Spin>
      </div>);
    }
}
HorizontalTable.defaultProps = {
    dataSource: {},
    prefixCls: 'w-table',
    size: 'default',
    bordered: 'solid',
    loading: false,
    locale: {},
    striped: true,
};
HorizontalTable.contextTypes = {
    antLocale: PropTypes.object,
};
